import { changeTitle } from '@/common/common'
export default {
    data(){
        return{
            tabIndex: 0,
            wait: 0,
            timer: null,
            codeHtml: '',
            loginJson:{
                phone: '',
                code: '',
                redirect:''
            },
            logBtnStyle: {},
            registerJson:{
                userName: '',
                phone: '',
                code: ''
            },
            regBtnStyle: {}
        }
    },
    mounted(){
        changeTitle('');
    },
    watch:{
        // 
        loginJson:{
            handler(val, oldVal){
                if(val.phone!=''&&val.code!=''){
                    this.logBtnStyle = { opacity: 1 };
                }else{
                    this.logBtnStyle = {};
                }
            },
            deep:true
        },
        registerJson:{
            handler(val, oldVal){
                if(val.userName!=''&&val.phone!=''&&val.code!=''){
                    this.regBtnStyle = { opacity: 1 };
                }else{
                    this.regBtnStyle = {};
                }
            },
            deep:true
        }
    },
    methods:{
        // 
        tab(index){
            clearTimeout(this.timer);
            this.wait = 0;
            this.timer = null;
            this.codeHtml = '';
            this.tabIndex = index;
        },
        // 
        getCode(tel){
            if(this.wait > 0){
                return
            }
            let myreg = /^[1][3,4,5,7,8][0-9]{9}$/; //
            if (tel == null || tel == ""||!myreg.test(tel)) {
                this.$vux.toast.show({
                    type:'text',
                    text: "",
                    position:'middle'
                })
                return;
            }
            let that = this;
            /* TODO  */
            this.$http.post('/user/get_code/',{phone:this.loginJson.phone}).then(res=>{
                if(res.status==200){
                    this.wait = 60;
                    clearTimeout(this.timer);
                    this.downTime();
                }
            })  
            
        },
        // 
        downTime() {
            if (this.wait == 0) {
                this.codeHtml = '';
                this.wait = 0;
            } else {
                this.codeHtml = ""+"(" + this.wait + ")";
                this.wait--;
                this.timer = setTimeout(() => {
                    this.downTime();
                }, 1000);
            }
        },
        // 
        login(){
            if(this.$route.query.from){
                this.loginJson.redirect = 'http://' + window.location.host + '/' + this.$route.query.from;
            }else{
                this.loginJson.redirect = 'http://' + window.location.host + '/home';
            }
            if(this.loginJson.phone!=''&&this.loginJson.code!=''){
                this.$http.post('/user/login/',this.loginJson).then(res=>{
                if(res.status==302){//
                    this.$vux.toast.show({
                        type:'text',
                        text: res.message,
                        position:'middle'
                    })
                    localStorage.setItem('token',res.data.token);
                    window.location = res.data.redirect_url;
                }else if(res.status==200){//
                    localStorage.setItem('token',res.data.token);
                    window.location = this.loginJson.redirect;
                }else{
                    this.$vux.toast.show({
                        type:'text',
                        text: res.message,
                        position:'middle'
                    })
                }
            })  
            }else{
                return false
            }
        },
        // 
        register(){
            if(this.registerJson.userName!=''&&this.registerJson.phone!=''&&this.registerJson.code!=''){

            }
        },
    }
}



