export default {
    props:['inputLocation'],
    data(){
        return {
            address_detail: null, //
            villegeName: null,
            userlocation: {lng: "", lat: ""},
        }
    },
    methods:{
        confirm(){
            this.$emit('getVillegeName',this.villegeName);
            this.$emit('updateIsShow', false);
        }
    },
    mounted(){
        this.$nextTick(function () {

                var th = this
                // Map
                var map = new BMap.Map("allmap");
                // ,
                var point = new BMap.Point(121.160724,31.173277); // 
                map.centerAndZoom(point, 17);
                map.enableScrollWheelZoom();
                var ac = new BMap.Autocomplete(    //
                    {
                        "input": "suggestId"
                        , "location": map
                    });
                var myValue
                ac.addEventListener("onconfirm", function (e) {    //
                    var _value = e.item.value;
                    myValue = _value.province + _value.city + _value.district + _value.street + _value.business;
                    th.villegeName = _value.business;
                    th.address_detail = myValue;
                    setPlace();
                });

                function setPlace() {
                    map.clearOverlays();    //
                    function myFun() {
                        th.userlocation = local.getResults().getPoi(0).point;    //
                        th.$emit('getLocation',th.address_detail,th.userlocation);
                        map.centerAndZoom(th.userlocation, 17);
                        map.addOverlay(new BMap.Marker(th.userlocation));    //
                    }

                    var local = new BMap.LocalSearch(map, { //
                        onSearchComplete: myFun
                    });
                    local.search(myValue);
                    

                    //
                    map.addEventListener("click",function(e){
                         //
                         console.log(th.userlocation.lng);
                         //
                         console.log(th.userlocation.lat);
                    })
                }

        })
    },
}



